import React from "react";
import {
  List,
  Datagrid,
  TextField
  // Create,
  // SimpleForm,
  // TextInput
} from "react-admin";

export const transactionList = props => (
  <List {...props}>
    <Datagrid>
      <TextField source="id" />
      <TextField source="Jemput" />
      <TextField source="ProdukList" />
      <TextField source="TambahKeranjang" />
      <TextField source="Konfirmasi" />
      <TextField source="DetailTransaksi" />
    </Datagrid>
  </List>
);
