import React from "react";
import {
  Radar,
  RadarChart,
  PolarGrid,
  ResponsiveContainer,
  PolarAngleAxis,
  PolarRadiusAxis
} from "recharts";

const data = [
  { subject: "Aki", A: 120, B: 110, fullMark: 150 },
  { subject: "Botol Oli", A: 98, B: 130, fullMark: 150 },
  { subject: "Kaleng", A: 86, B: 130, fullMark: 150 },
  { subject: "Botol Kaca", A: 99, B: 100, fullMark: 150 },
  { subject: "Botol Plastik", A: 85, B: 90, fullMark: 150 },
  { subject: "Plastik", A: 65, B: 85, fullMark: 150 }
];

function TwoLevelPieChart() {
  return (
    <ResponsiveContainer width="99%" height={250}>
      <RadarChart cx={230} cy={130} outerRadius={100} data={data}>
        <PolarGrid />
        <PolarAngleAxis dataKey="subject" />
        <PolarRadiusAxis />
        <Radar
          name="Mike"
          dataKey="A"
          stroke="#8884d8"
          fill="#8884d8"
          fillOpacity={0.6}
        />
      </RadarChart>
    </ResponsiveContainer>
  );
}

export default TwoLevelPieChart;
