import React from "react";
import {
  LineChart,
  Line,
  ResponsiveContainer,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip
} from "recharts";
const data = [
  { name: "Senin", uv: 5, pv: 9 },
  { name: "Selasa", uv: 4, pv: 7 },
  { name: "Rabu", uv: 3, pv: 6 },
  { name: "Kamis", uv: 2, pv: 5 },
  { name: "Jum'at", uv: 3, pv: 3 },
  { name: "Sabtu", uv: 4, pv: 2 },
  { name: "Minggu", uv: 5, pv: 1 }
];
function SimpleAreaChart() {
  return (
    <ResponsiveContainer width="99%" height={150}>
      <LineChart
        data={data}
        syncId="anyId"
        margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Line type="monotone" dataKey="uv" stroke="#8884d8" fill="#8884d8" />
      </LineChart>
    </ResponsiveContainer>
  );
}
export default SimpleAreaChart;
