// in src/Dashboard.js
import React from "react";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import { Title } from "react-admin";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import SameDataComposedChart from "./composedChart";
import SimpleAreaChart from "./effectOpacity";
import TwoLevelPieChart from "./radar";
const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

function CenteredGrid(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Grid container spacing={24}>
        <Grid item xs={12}>
          <Card>
            <Title title="Dashboard" />
            <CardHeader
              style={{ textAlign: "center" }}
              title="Selamat Datang Di Tampah Nasabah"
            />
          </Card>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Keseluruhan Sampah (per-bulan, /kg)
            <SameDataComposedChart />
          </Paper>
        </Grid>
        <Grid item xs={12} md={6} alignItems={"center"}>
          <Paper className={classes.paper}>
            Data Bank Sampah Keseluruhan (per-minggu, /kg)
            <SimpleAreaChart />
          </Paper>
        </Grid>
        {/* <Grid item xs={12}>
          <Paper className={classes.paper}>
            Data Sampah Keseluruhan (per-bulan, /ton)
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>Data Fluktuasi Bank Sampah</Paper>
        </Grid> */}
        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Data Sampah Nasabah
            <TwoLevelPieChart />
          </Paper>
        </Grid>
        {/* <Grid item xs={12}>
          <Paper className={classes.paper}>
            Data Akumulasi Sampah Regional
          </Paper>
        </Grid> */}
        {/* <Grid item xs={12}>
          <Paper className={classes.paper}>Rating Sampah Regional</Paper>
        </Grid> */}
      </Grid>
    </div>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
