import React from "react";

import {
  ComposedChart,
  Line,
  Bar,
  ResponsiveContainer,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";

const data = [
  { name: "Januari", uv: 590, pv: 800, amt: 1400 },
  { name: "Februari", uv: 868, pv: 967, amt: 1506 },
  { name: "Maret", uv: 1397, pv: 1098, amt: 989 },
  { name: "April", uv: 1480, pv: 1200, amt: 1228 },
  { name: "Mei", uv: 1520, pv: 1108, amt: 1100 },
  { name: "Juni", uv: 1400, pv: 680, amt: 1700 }
];

function SameDataComposedChart() {
  return (
    <ResponsiveContainer width="99%" height={150}>
      <ComposedChart
        data={data}
        margin={{ top: 20, right: 20, bottom: 20, left: 20 }}
      >
        <CartesianGrid stroke="#f5f5f5" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Bar dataKey="uv" barSize={20} fill="#413ea0" />
        <Line type="monotone" dataKey="uv" stroke="#ff7300" />
      </ComposedChart>
    </ResponsiveContainer>
  );
}
export default SameDataComposedChart;
