// in src/myAccount.js
import React from "react";
import {
  List,
  Datagrid,
  TextField,
  Create,
  SimpleForm,
  TextInput,
  Edit,
  DisabledInput,
  ReferenceInput,
  SelectInput,
  LongTextInput,
  Responsive,
  SimpleList,
  EditButton
} from "react-admin";

export const myAccountList = props => (
  <List {...props} filters={<myAccountFilter />}>
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.title}
          secondaryText={record => `${record.views} views`}
          tertiaryText={record =>
            new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <TextField source="nama" />
          {/* <TextField source="nama" /> */}
          <TextField source="handphone" />
          <TextField source="alamat" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);
export const myAccountCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <TextInput source="nama" />
      <TextInput source="handphone" />
      <TextInput source="alamat" />
      <TextInput source="rekening" />
    </SimpleForm>
  </Create>
);

export const myAccountEdit = props => (
  <Edit title={<myAccountTitle />} {...props}>
    <SimpleForm>
      <DisabledInput source="nama" />
      <ReferenceInput label="nama" source="nama" reference="myAccount">
        <SelectInput optionText="name" />
      </ReferenceInput>
      <DisabledInput source="hanphone" />
      <LongTextInput source="alamat" />
      <TextInput source="rekening" />
    </SimpleForm>
  </Edit>
);
