// in src/App.js
import React from "react";
import { Admin, Resource } from "react-admin";
import jsonServerProvider from "ra-data-json-server";
import Dashboard from "./Dashboard";
// import authProvider from "./authProvider";
import { myAccountList, myAccountCreate, myAccountEdit } from "./myAccount";
import face from "@material-ui/icons/Face";
import { scheduleList } from "./schedule";
import event from "@material-ui/icons/Event";
import { transactionList } from "./transaction";
import payment from "@material-ui/icons/Payment";
import { checkTransactionList } from "./checkTransaction";
import redeem from "@material-ui/icons/Redeem";
import { listPriceList } from "./listPrice";
import add_shopping_cart from "@material-ui/icons/AddShoppingCart";
import { featureList } from "./feature";
import menu from "@material-ui/icons/Menu";

const dataProvider = jsonServerProvider(
  //"https://my-json-server.typicode.com/rusfandi/jsonserver"
  "http://localhost:3000"
);

const App = () => (
  <Admin
    dashboard={Dashboard}
    // authProvider={authProvider}
    dataProvider={dataProvider}
  >
    <Resource
      name="myAccount"
      list={myAccountList}
      create={myAccountCreate}
      edit={myAccountEdit}
      icon={face}
      options={{ label: "Akun" }}
    />
    <Resource
      name="schedule"
      list={scheduleList}
      icon={event}
      options={{ label: "Jadwal" }}
    />
    <Resource
      name="transaction"
      list={transactionList}
      // create={transactionCreate}
      icon={payment}
      options={{ label: "Transaksi" }}
    />
    <Resource
      name="checkTransaction"
      list={checkTransactionList}
      icon={redeem}
      options={{ label: "CekTransaksi" }}
    />
    <Resource
      name="listPrice"
      list={listPriceList}
      // create={listPriceCreate}
      icon={add_shopping_cart}
      options={{ label: "Harga" }}
    />
    <Resource
      name="feature"
      list={featureList}
      // create={featureCreate}
      icon={menu}
      options={{ label: "Fitur" }}
    />
  </Admin>
);
export default App;
