import React from "react";
import {
  List,
  Datagrid,
  TextField
  // TextInput,
  // Create,
  // SimpleForm
} from "react-admin";

export const featureList = props => (
  <List {...props}>
    <Datagrid>
      <TextField source="id" />
      <TextField source="saldo" />
      <TextField source="topup" />
      <TextField source="t_bayar" />
      <TextField source="t_beli" />
      <TextField source="t_merchant" />
      <TextField source="t_warung" />
      <TextField source="t_jajan" />
      <TextField source="t_chat" />
      <TextField source="t_belajar" />
      <TextField source="t_games" />
    </Datagrid>
  </List>
);
