import React from "react";
import { List, Datagrid, TextField } from "react-admin";

export const scheduleList = props => (
  <List {...props}>
    <Datagrid>
      <TextField source="jadwal" />
      <TextField source="deskripsi" />
      <TextField source="tanggal" />
      <TextField source="tempat" />
    </Datagrid>
  </List>
);
