import React from "react";
import {
  List,
  Datagrid,
  TextField
  //   Create,
  //   SimpleForm,
  //   TextInput
} from "react-admin";

export const listPriceList = props => (
  <List {...props}>
    <Datagrid>
      <TextField source="id" />
      <TextField source="produk" />
      <TextField source="harga" />
      <TextField source="satuan" />
      <TextField source="namaBankSampah" />
      <TextField source="updateTerakhir" />
    </Datagrid>
  </List>
);
